* {
  margin:0;
  padding:0;
  border:0;
}

#root {
text-align: center;
background: rgb(34, 107, 60);
width: 100vw;
height: 100vh;
}

#main-logo {
max-width: 70vw;
z-index: -1;
}

#login {
position: absolute;
bottom: 8%;
left: 20%;
}

#register {
position: absolute;
bottom: 8%;
right: 18%;
}

.landing-button {
width: 160px;
height: 60px;
font-size: 25px;
background:rgb(185, 185, 185);
border: 3px solid white;
border-radius: 3px;
cursor: pointer;
}

.landing-button:hover {
border-color: black;
}

#login-logo {
width: 40vw;
}

a {
text-decoration: none;
}

#games-lobby-items{
padding-top: 10px;
font-size: 1.2em;
}

#games-lobby-items button {
background: rgb(84, 206, 84);
padding: 5px;
border-radius: 20px;
}

.user-input input {
margin-bottom: 4%;
padding: 15px;
background-color: rgb(184, 184, 184);
border: solid white 3px;
color: black;
font-size: 20px;
border-radius: 3px;
}

.user-input input[type=submit] {
width: 50%;
cursor: pointer;
}
input[type=submit]:hover {
border-color: black;
width: 50%;
}

form {
margin: 0 35%;
text-align: center;
}

#register-logo {
width: 30vw;
}

.player{
width: 105px;
height: 80px;
padding: 0px;
margin: 5px;
}

.play-text-info{
margin: auto;
border-width: 1px;
text-align: center;
width: 100px;
height: 40px;
padding-left: 10px;
border-style: solid;
border-radius: 15px;
border-color: white;
font-family: Arial, Helvetica, sans-serif;
padding: 1px; 
background-color: black;
color: white;
float:left;
position: absolute;
top: 60px;
opacity: .8;
}

.not-in-hand {
opacity: .5 !important;
}

#hole-card{
width: 50px;
z-index: -1;
}

#community-cards{
position: absolute;
top: 30%;
left: 40%;
}

#table{
background-color: green;
width: 100vw;
height: 80vh;
border-radius: 40%;
}

.join-button{
margin-top: 20px;
font-size: 18px;
background-color: grey;
border: none;
padding: 15px 10px;
border-radius: 10px;
margin-top: 40px;
cursor: pointer;
}

.join-button:hover{
border: 1px black solid;
}

#player0{
position: absolute;
left: 74%;
}

#player1{
position: absolute;
left: 90%;
top: 15%;
}

#player2{
