}

.player-turn-menu {
display: flex;
justify-content: space-evenly;
width: 50vw;
height: 20vh;
background-color: rgb(49, 71, 46);
position: fixed;
bottom: 0;
right: 0;
border-radius: 5px;
}

.player-turn-menu button {
padding: 7% 11%;
margin: 2% 1%;
font-size: 25px;
background-color: rgb(97, 131, 97);
color: white;
border-radius: 3px;
cursor: pointer;
}

.player-turn-menu button:hover {
background-color: rgb(160, 199, 160);
}
